import React from 'react'
import Link from 'gatsby-link'
import WeekOneImage from '../assets/images/HHY-week1.png';


const WeeklyCards = ({
    data: {
        allContentfulLesson: {
            edges
        }
    }
}) => (
    <section className="section has-background-light">
        <div className="container">
            <div className="columns is-multiline">

            {edges.map((week, i) => 
                <div className="column is-one-third" key={i}>
                    <div className="card" >
                        <header className="card-header">
                            <Link to={week.node.slug}>
                                <h1 className="card-header-title">{week.node.title}</h1>
                            </Link>
                        </header>
                        
                        <div className="card-image">
                            <img src={WeekOneImage} />
                            
                        </div>
                        <div className="card-content">
                            <p>{week.node.description}</p>
                        </div>
                        <footer className="card-footer">
                            <Link to={week.node.slug} className="card-footer-item">Weekly Lesson</Link>
                        </footer>
                        <footer className="card-footer">
                            <a href={week.node.youtube} className="card-footer-item">
                                <span className="icon">
                                    <i className="fab fa-youtube-square "></i>
                                </span> YouTube
                            </a>
                                <a href={week.node.soundcloud} className="card-footer-item">
                                    <span className="icon">
                                        <i className="fab fa-soundcloud "></i>
                                    </span> Soundcloud
                                </a>
                        </footer>
                    </div>
                </div>
            )}

            </div>
        </div>
    </section>
)

export default WeeklyCards


